:root {
  --primary: #ffffff;
  --secondary: #005346;
  --primary-foreground: #d9d9d9;
  --secondary-foreground: rgba(0, 83, 70, 0.3);
  --inactive: rgba(0, 83, 70, 0.1);
}
.dark {
  --primary: #000000;
  --secondary: #ffffff;
  --primary-foreground: #d9d9d9;
  --secondary-foreground: #424242;
  --inactive: rgba(255, 255, 255, 0.1);
}
body {
  background: var(--primary);
  color: var(--secondary);
  font-family: var(--font-montserrat);
}
h1 {
  font-size: 22px;
  --tw-font-weight: var(--font-weight-bold);
  font-weight: var(--font-weight-bold);
}
h2 {
  font-size: 16px;
  --tw-font-weight: var(--font-weight-semibold);
  font-weight: var(--font-weight-semibold);
}
h3 {
  font-size: 14px;
  --tw-font-weight: var(--font-weight-semibold);
  font-weight: var(--font-weight-semibold);
}
h4 {
  font-size: 13px;
  --tw-font-weight: var(--font-weight-semibold);
  font-weight: var(--font-weight-semibold);
}
h5 {
  font-size: 12px;
  --tw-font-weight: var(--font-weight-normal);
  font-weight: var(--font-weight-normal);
}
p {
  font-size: 12px;
  --tw-font-weight: var(--font-weight-normal);
  font-weight: var(--font-weight-normal);
}
button {
  font-size: 13px;
  --tw-font-weight: var(--font-weight-bold);
  font-weight: var(--font-weight-bold);
}
.react-grid-layout {
  padding: 0 !important;
  margin: 0 !important;
}
.react-grid-item {
  padding: 0 !important;
}
.hotspot {
  background: rgba(0, 123, 255, 0.9);
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.comment-tooltip {
  position: absolute;
  background-color: white;
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 6px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
  width: 200px;
  z-index: 999;
  font-size: 12px;
}
.hotspot:hover .comment-tooltip {
  visibility: visible;
  opacity: 1;
}
.split-wrapper > .gutter {
  background-color: var(--secondary-foreground);
  position: relative;
  cursor: row-resize;
}
.split-wrapper > .gutter::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 4px;
  background-color: var(--primary);
  border-radius: 2px;
  box-shadow: 0 -2px 0 var(--secondary-foreground), 0 2px 0 var(--secondary-foreground);
  transition: width 0.2s, opacity 0.2s;
  opacity: 0.5;
}
.split-wrapper > .gutter:hover::after {
  width: 40px;
  opacity: 1;
}
